import { mapGetters, mapActions} from 'vuex'
import BScroll from 'better-scroll'
import { getProvince, getCity, getArea, addAddressnew} from 'api/api'
export default {
  data() {
    return {
      status: false,
      default: 0,
      province_id: '2182',
      name: '',
      address: '',
      phone: '',
      city_id: '2268',
      area_id: '2270',
      province:[],
      city: [],
      area: [],
      provinceName: '山东',
      cityName: '青岛',
      areaName: '市北',
      defaultArea: '省份、城市、区县',
      fold: true
    }
  },
  created() {
    this._getProvince()

    this.$watch('phone', (newPhone) => {
      this.phone = newPhone
    })
    this.$watch('name', (newName) => {
      this.name = newName
    })
    this.$watch('address', (newAddress) => {
      this.address = newAddress
    })
  },
  mounted () {

  },
  computed: {
    listShow() {
      let show = !this.fold;
      if (show) {
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.listContent, {
              click: true
            })
          } else {
            this.scroll.refresh();
          }
        })
      }
      return show
    },
    ...mapGetters([
      'userAccount'
    ])
  },
  methods: {
    back() {
      this.$router.back()
    },
    save() {
      let token = this.userAccount.token
      let uid = this.userAccount.uid
      let province = this.province_id
      let city = this.city_id
      let area = this.area_id
      let name = this.name
      let adress= this.address
      let phone = this.phone
      let default1 = this.default
      addAddressnew(token,uid,province,city,area,adress,name,phone,default1).then((res)=>{
        // 保存地址vuex
        let name = this.name
        let phone = this.phone
        let province = this.provinceName
        let province_id = this.province_id
        let city = this.cityName
        let city_id = this.city_id
        let area = this.areaName
        let area_id = this.area_id
        let default1 = this.default
        let address = this.address
        let whole = `${province}${city}${area}${address}`
        let obj = [{
          name:name,
          phone:phone,
          province:province,
          province_id:province_id,
          city: city,
          city_id: city_id,
          area: area,
          area_id: area_id,
          default: default1,
          address: address
        }]
        this.saveAddress(obj)
        this.$router.back()
      })
    },
    setDefault() {
      if(this.status) {
        this.status = false
        this.default = 0
      }else{
        this.status = true
        this.default = 1
      }
    },
    _getProvince(reflash) {
      if(reflash === 1){
        this.city = []
        this.area = []
      }
      getProvince().then((res)=>{
        if(res.status === 1) {
          this.province = res.msg
        }
      })
    },
    _getCity(name,id,reflash) {
      if(reflash === 1) {
        this.province = []
        this.area = []
      }
      let province = id
      this.province_id = id
      this.provinceName = name
      this.cityName = '请选择',
      this.areaName = '请选择'
      getCity(province).then((res)=>{
        if(res.status === 1) {
          this.city = res.msg
        }
      })
    },
    _getArea(name,id) {
      let city = id
      this.city_id = id
      this.cityName = name
      this.areaName = '请选择'
      getArea(city).then((res)=>{
        if(res.status === 1) {
          this.city = []
          this.area = res.msg
        }
      })
    },
    _setArea(name,id) {
      this.areaName = name
      this.area_id = id
    },
    hideList() {
      this.fold = true
    },
    yesBtn() {
      this.fold = true
      this.defaultArea = `${this.provinceName}-${this.cityName}-${this.areaName}`
    },
    showCityPicker() {
      this.fold = !this.fold
    },
    ...mapActions([
      'saveAddress'
    ])
  }
}



